body, div, p, h1, h2, h3, h4, h5, h6, fieldset, textarea, ul, ol, li, dl, dt, dd, figure {
	margin: 0;
	padding: 0;
}

ul {
	list-style: none;
}

fieldset {
	border: 0;
}

input {
	padding: 0;
	margin: 0;
	border: none;
}

button {
	overflow:visible;
	cursor: pointer;
	margin: 0;
	border: 0;
	padding: 0;
	background: none;
}

select {
	cursor: pointer;
}

a {
	text-decoration: none;
	color: #000;
}

body {
	font-size: 12px;
}

h1, h2, h3, h4, h5, h6 {
	font-size:1rem;
}

em {
	font-style: normal;
}

img {
	vertical-align: top;
	border: 0;
}


textarea {
	vertical-align:top;
	border:0;
}
